import React,{useState} from 'react'
export default function TextForm(props) {
    const [text, setText] = useState('');
  const Clicked=()=>{
    setText("you have change the text")
    props.Showalert("changed the text successfully","success");
  }
    const ClickedOn=(event)=>{
    setText(event.target.value)
    props.Showalert("Typing starts","success");
    
  }
  return (
    <>
<div class="mb-3">
  <h1 style={{color:props.mode==='light'?'dark':'black'}}>{props.heading}</h1>
  <textarea class="form-control" value={text} onChange={ClickedOn} style={{backgroundColor:props.mode==='light'?'dark':'darkgrey'}}  id="mybox" rows="8" ></textarea>
  <button className="btn btn-primary my-3" onClick={Clicked}>convert to upper case</button>
</div>
<div className="container" style={{color:props.mode==='light'?'black':'white'}} >
  <h1 className='my-3'>
    words {text.split(" ").length} and characters {text.length}
  </h1>
</div>
</>
  )
}
